// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
import createTemplateModal from '@/components/modal/checkOptionModal/createTemplateModal';
import createTemplateRelationModal from '@/components/modal/checkOptionModal/createTemplateRelationModal';
import Sortable from 'sortablejs';
import { MSG, BTN_TEXT, STATUS_CODE, TITLE, CONSTANTS_CHECK } from '@/utils/constantsFile';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  checkTemplateDeleteByIdsAction,
  checkItemsGetAllAction,
  checkTemplateInsertAction,
  checkTemplateUpdateByIdAction,
  checkTemplateGetAllAction,
  checkItemTemplateRelationshipGetListCheckTemplateRelationshipItemAction,
  checkItemTemplateRelationshipInsertOrUpdateBatchAction,
  checkItemTemplateRelationshipDeleteByIdAction
} from '@/api/checkConfig.js';
export default {
  components: {
    createTemplateModal,
    createTemplateRelationModal,
    // 加载table组件
    VXETable,
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      // EQUIPMENT_DATA: EQUIPMENT_DATA,
      // 模态框标题
      templateEditTitle: '',
      // 模态框标题
      itemRealtionTitle: CONSTANTS_CHECK.CHOOSE_CHECK_POINT,
      // 是否显示模态框
      showCreateTemp: false,
      // table加载动画
      tableloading: false,
      // 是否新增
      isAddItem: true,
      // 选中数据
      selectTemplate: null,
      // 勾选数据
      selectRecords: [],
      // table配置
      templateTableData: {
        data: [],
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          // {
          //   type: 'radio',
          //   align: 'center',
          //   visible: true,
          //   width: '50'
          // },
          { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'templateName',
            title: CONSTANTS_CHECK.TEMPLATE_NAME,
            align: 'center'
          },
          {
            field: 'scenesName',
            title: CONSTANTS_CHECK.SCENES_NAME,
            align: 'center'
          }
        ]
      },
      tempListData: [],
      tempDisableList: [],
      activeTemplate: null,
      // table加载动画
      relationShipTableDataLoading: false,
      // table数据
      relationShipTableData: [],
      showCreateTempRelation: false
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.showCreateTempModal(row);
          break;
        case 'delete':
          this.deleteTemplate(row);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 获取模板数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    getTemplateData() {
      this.tableloading = true;
      checkTemplateGetAllAction({
        current: this.templateTableData.page.currentPage,
        size: this.templateTableData.page.pageSize
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableloading = false;
          this.templateTableData.page.total = res.total;
          this.templateTableData.data = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 显示模板模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showCreateTempModal(data) {
      this.templateEditTitle = CONSTANTS_CHECK.ADD_CHECK_TEMPLATECONFIG;

      this.showCreateTemp = true;
    },
    /**
     * @description: 显示模板模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showUpdateTempModal() {
      var data = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.templateTable.getRadioRecord();
      }
      if (data) {
        this.isAddItem = false;
        this.selectTemplate = data;
        this.templateEditTitle = CONSTANTS_CHECK.UPDATE_CHECK_TEMPLATECONFIG;
      } else {
        this.templateEditTitle = CONSTANTS_CHECK.ADD_CHECK_TEMPLATECONFIG;
      }
      this.showCreateTemp = true;
    },
    /**
     * @description: 取消模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clickModalCancel() {
      this.showCreateTempRelation = false;
      this.showCreateTemp = false;
      this.isAddItem = true;
      this.selectTemplate = null;
    },
    cellClickEvent({ column }) {
    },
    cellClickEvent1({ column }) {
    },
    /**
     * @description: 新增或修改
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    addOrUpdateMaintainTemplate(data) {
      this.clickModalCancel();
      if (data.isAdd) {
        checkTemplateInsertAction(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getTemplateData();
            showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      } else {
        checkTemplateUpdateByIdAction(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getTemplateData();
            showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      }
    },
    toDelete() {
      var row = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.templateTable.getRadioRecord();
      }
      this.deleteTemplate([row]);
    },
    /**
     * @description: 删除模板
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    deleteTemplate(data) {
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteTemplate(data);
        })
        .catch(() => { });
    },
    /**
     * @description: 执行删除模板
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    setDeleteTemplate(data) {
      var ids = [];
      data.forEach((item) => {
        ids.push(item.templateId);
      });
      checkTemplateDeleteByIdsAction({ ids: ids }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getTemplateData();
          this.activeTemplate = null;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 右边保养项关联表
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    handleSelectTemplate(data) {
      this.activeTemplate = data;
      this.getRelationshipItemByID(data.templateId);
    },
    /**
     * @description: 根据保养模版查询关联保养项
     * @param id 数据
     * @return: 无
     * @author: ZJM
     */
    getRelationshipItemByID(id) {
      this.relationShipTableDataLoading = true;
      var self = this;
      checkItemTemplateRelationshipGetListCheckTemplateRelationshipItemAction({ templateId: id }
      ).then(res => {
        self.relationShipTableDataLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          self.relationShipTableData = res.rows;
          self.tempDisableList = [];
          res.rows.forEach((item) => {
            self.tempDisableList.push(item.itemId);
          });
          self.rowDrop();
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        self.relationShipTableDataLoading = false;
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 打开选择点检项modal
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showCreateTempRelationModal() {
      checkItemsGetAllAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tempListData = res.rows;
          this.showCreateTempRelation = true;
          this.$refs.templateRelationModal.tableDatas = JSON.parse(JSON.stringify(res.rows));
          this.$refs.templateRelationModal.showList = JSON.parse(JSON.stringify(res.rows));
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 插入一条保养项
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    insertMaintainceItems(data) {
      console.log(data);
      var self = this;
      data.forEach((item) => {
        item.templateId = self.activeTemplate.templateId;
        self.relationShipTableData.push(item);
      });
      // console.log(this.relationShipTableData);
      this.tempDisableList = { ...this.relationShipTableData };
      this.clickModalCancel();
    },
    /**
     * @description: 保存保养项排序
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitTempRelation() {
      var params = [];
      for (var i = 0; i < this.relationShipTableData.length; i++) {
        var item = {
          relationshipId: this.relationShipTableData[i].relationshipId,
          templateId: this.relationShipTableData[i].templateId,
          itemId: this.relationShipTableData[i].itemId,
          sort: i + 1
        };
        params.push(item);
      }
      checkItemTemplateRelationshipInsertOrUpdateBatchAction(null, params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.getRelationshipItemByID(this.activeTemplate.templateId);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 删除保养项
     * @param data
     * @return: 无
     * @author: ZJM
     */
    deleteTemplateRelationItem() {
      var data = {};

      if (!this.$refs.sortTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.sortTable.getRadioRecord();
      }
      if (data.relationshipId) {
        checkItemTemplateRelationshipDeleteByIdAction({
          id: data.relationshipId
        }).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getRelationshipItemByID(this.activeTemplate.templateId);
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      } else {
        var index = 0;
        for (var i = 0; i < this.relationShipTableData.length; i++) {
          if (this.relationShipTableData[i].templateId === data.templateId) {
            index = i;
            break;
          }
        }
        this.relationShipTableData.splice(index, 1);
      }
    },
    /**
     * @description: 表拖拽
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    rowDrop() {
      var self = this;
      this.$nextTick(() => {
        var xTable = this.$refs.sortTable;
        this.sortable = Sortable.create(xTable.$el.querySelector('.body--wrapper>.vxe-table--body tbody'), {
          handle: '.drag-btn',
          onEnd: ({ newIndex, oldIndex }) => {
            var currRow = self.relationShipTableData.splice(oldIndex, 1)[0];
            self.relationShipTableData.splice(newIndex, 0, currRow);
          }
        });
      });
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handleTemplateTablePageChange(currentPage, pageSize) {
      this.templateTableData.page.currentPage = currentPage;
      this.templateTableData.page.pageSize = pageSize;
      this.getTemplateData();
    },
    /**
     * @description: table选中数据
     * @param records 数据
     * @return: 无
     * @author: ZJM
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    refreshTable() {
      this.getTemplateData();
    }
  },
  watch: {
    relationShipTableData(val, oldVal) {
    }
  },
  created() {
    this.getTemplateData();
  },
  beforeDestroy() {
    if (this.sortable) {
      this.sortable.destroy();
    }
  }
};
