<template>
  <el-dialog
    :class="['create-time-option',$store.state.dialogMarginClass]"
    width="65%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <el-form
        ref="updateForm"
        :label-width="'100px'"
        :model="formUpdate"
        :rules="rulesUpdate"
        class="dialogForm updateForm"
      >
        <el-form-item :label="CONSTANTS_CHECK.SCENES_NAME+':'" prop="scenesName">
          <el-input v-model="formUpdate.scenesName"></el-input>
        </el-form-item>
        <el-form-item :label="CONSTANTS_CHECK.EXTEND_TIME+':'" prop="extensionTime">
          <el-input v-model="formUpdate.extensionTime"></el-input>
        </el-form-item>
        <div class="timescope">
          <el-form-item :label="CONSTANTS_CHECK.CHECK_TIME+':'">
            <el-select class="selectplace" v-model="formUpdate.shiftLength">
              <el-option
                v-for="item in shiftOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <div>
            <el-row
              class="shiftDiv"
              :gutter="24"
              v-for="(item,index) in formUpdate.shiftLength"
              :key="index"
            >
              <el-col :span="6">
                <el-form-item
                  :label="CONSTANTS_CHECK.SHIFT_NAME+':'"
                  :prop="`shiftInformationList.${index}.shiftName`"
                  :rules="rulesUpdate.shiftName"
                >
                  <el-input v-model="formUpdate.shiftInformationList[index].shiftName"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item
                  :label="CONSTANTS_CHECK.UP_SHIFT+':'"
                  :prop="`shiftInformationList.${index}.shiftStartTime`"
                  :rules="rulesUpdate.shiftStartTime"
                >
                  <el-time-picker
                    v-model="formUpdate.shiftInformationList[index].shiftStartTime"
                    format="HH:mm"
                    value-format="HH:mm"
                  ></el-time-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item
                  :label="CONSTANTS_CHECK.DOWN_NAME+':'"
                  :prop="`shiftInformationList.${index}.shiftEndTime`"
                  :rules="rulesUpdate.shiftEndTime"
                >
                  <el-time-picker
                    v-model="formUpdate.shiftInformationList[index].shiftEndTime"
                    format="HH:mm"
                    value-format="HH:mm"
                  ></el-time-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                   <el-button @click="toggleColorTask(index)" >编辑时间段颜色</el-button>
              </el-col>
              <el-col v-if="formUpdate.shiftInformationList[index].shiftShowColor" :span="24">
                <el-col class="dateTimeDetail" :span="24">
                  <div class="dateColorLine"  v-for="(item,indexb) in formUpdate.shiftInformationList[index].shifColorArr" :key="indexb" >
                        <el-color-picker size="small" v-model="formUpdate.shiftInformationList[index].shifColorArr[indexb].color"></el-color-picker>
                        <el-time-picker
                          style="margin-left:40px;"
                          size="mini"
                          v-model="formUpdate.shiftInformationList[index].shifColorArr[indexb].sTime"
                          format="HH:mm"
                          value-format="HH:mm"
                        ></el-time-picker>
                        <span style="margin:0 10px;" >至</span>
                        <el-time-picker
                          size="mini"
                          v-model="formUpdate.shiftInformationList[index].shifColorArr[indexb].eTime"
                          format="HH:mm"
                          value-format="HH:mm"
                        ></el-time-picker>
                        <el-button style="margin:0 10px;" size="mini"  icon="el-icon-plus" circle @click="addColorTime(formUpdate.shiftInformationList[index].shifColorArr)" ></el-button>
                        <el-button size="mini"  icon="el-icon-minus" circle @click="minusColorTime(formUpdate.shiftInformationList[index].shifColorArr,indexb)" ></el-button>
                  </div>
                </el-col>
              </el-col>
            </el-row>
            <span class="message" >*所有班次时间不得超过24小时，单班次时间不得超过{{24/formUpdate.shiftLength}}小时</span>
          </div>
        </div>
        <el-form-item :label="CONSTANTS_CHECK.REMARK+':'" prop="scenesRemark" >
          <el-input v-model="formUpdate.scenesRemark" type="textarea" :rows="3"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// import func from '../../../vue-temp/vue-editor-bridge';
// import moment from 'moment';
import { BTN_TEXT, MSG, CONSTANTS_CHECK } from '@/utils/constantsFile';
export default {
  name: 'CreateMaintainceOptionModal',
  props: {
    // 模态框标题
    title: { type: String, default: '' },
    // 取消按钮
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确定按钮
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 是否显示模态框
    showDialog: { type: Boolean, default: false },
    // 是否新增
    isAdd: { type: Boolean, default: true },
    // 模态框数据
    formUpdate: { type: Object, default: () => {} },
    // 取消事件
    handleCancel: Function,
    // 保存事件
    handleConfirm: Function,
    // 关闭事件
    handleClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 表单验证
      rulesUpdate: {
        scenesName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 10, message: '长度不得大于10个字符' }
        ],
        extensionTime: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        shiftName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 10, message: '长度不得大于10个字符' }
        ],
        shiftStartTime: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        shiftEndTime: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        scenesRemark: [
          { max: 100, message: '长度不得大于100个字符' }
        ]
      },
      // 班次数据
      shiftOptions: [
        {
          label: CONSTANTS_CHECK.ONE_SHIFT,
          value: 1
        },
        {
          label: CONSTANTS_CHECK.TWO_SHIFT,
          value: 2
        },
        {
          label: CONSTANTS_CHECK.THREE_SHIFT,
          value: 3
        },
        {
          label: CONSTANTS_CHECK.FOUR_SHIFT,
          value: 4
        }
      ],
      color1: '#409EFF'
    };
  },
  methods: {
    addColorTime(param) {
      param.push({
        sTime: '',
        eTime: '',
        color: '#1b0d74'
      });
    },
    minusColorTime(param, index) {
      // this.formUpdate.shiftInformationList[index].shifColorArr.array.splice(index,howmany)
      if (param.length !== 1) {
        param.splice(index, 1);
      }
    },
    toggleColorTask(index) {
      this.formUpdate.shiftInformationList[index].shiftShowColor = !this.formUpdate.shiftInformationList[index].shiftShowColor;
    },
    /**
     * @description: 点击取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
      // this.clearData();
    },
    /**
     * @description: 保存
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    confirmAction: function() {
      this.$refs.updateForm.validate(valid => {
        // 若验证通过
        if (valid) {
          this.handleConfirm(this.formUpdate);
          // this.clearData();
        }
      });
    },
    /**
     * @description: 点击关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
      // this.clearData();
    }
  },
  watch: {
    /**
     * @description: 监听数据
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    showDialog(val) {
      if (!val) {
        this.$refs.updateForm.resetFields();
      } else {
        setTimeout(() => {
          this.$refs.updateForm.clearValidate();
        }, 0);
      }
    }
  },
  mounted() {}
};
</script>
