/*
 * @Descripttion: 点检场景配置
 * @version:
 * @Author: ZJM
 * @Date: 2020-06-17 09:41:01
 * @LastEditors: ZJM
 * @LastEditTime: 2022-07-07 15:17:45
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// import { maintainceOptionService } from '@/services/maintainceOption.service';
import CreateScenesModal from '@/components/modal/checkOptionModal/createScenesModal';
import createScenesModals from '@/components/modal/checkOptionModal/createScenesModal_rz';
import { SYSTEM_CONFIG } from '@/utils/ajaxConfigure';
// 导入常量
import { PAGE_TITLE, BTN_TEXT, MSG, TITLE, STATUS_CODE, CONSTANTS_CHECK } from '@/utils/constantsFile';
// API
import {
  scenesGetPageScenesAction,
  scenesInsertOrUpdateScenesAndShiftAction,
  scenesDeleteByIdsAction
} from '@/api/checkConfig.js';
import {
  showWarningMessage,
  showSuccessMessage
} from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    CreateScenesModal,
    createScenesModals
  },
  props: {
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 模态框标题
      modalTitle: '',
      // 模态框数据
      showScenes: {
        show: false,
        show2: false,
        data: null
      },
      // 是否新增
      isAddItem: true,
      // 旧数据
      oldRow: {},
      // 选中的数据
      selectCase: {
        scenesName: '',
        extensionTime: 0,
        shiftLength: 1,
        shiftInformationList: [{
          shiftName: '',
          shiftStartTime: '',
          shiftEndTime: '',
          shiftSort: 0
        }]
      },
      // 批量选中数据
      selectRecords: [],
      // table加载动画
      tableloading: false,
      // table配置
      scenesTable: {
        data: [],
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          // { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'scenesName',
            title: CONSTANTS_CHECK.SCENES_NAME,
            align: 'center'
          },
          {
            field: 'extensionTime',
            title: CONSTANTS_CHECK.EXTEND_TIME,
            align: 'center'
          },
          {
            field: 'shiftInformationList',
            title: CONSTANTS_CHECK.CHECK_TIME,
            align: 'center',
            formatter: this.$options.filters.formatShiftTime
          },
          {
            field: 'scenesRemark',
            title: CONSTANTS_CHECK.REMARK,
            align: 'center'
          }
        ]
      },
      CHECK_IMG: false
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.showScenesModal(row);
          break;
        case 'delete':
          this.deleteScenes(row);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 分页获取点检场景
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    getScenesData() {
      this.tableloading = true;
      scenesGetPageScenesAction({
        current: this.scenesTable.page.currentPage,
        size: this.scenesTable.page.pageSize
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableloading = false;
          this.scenesTable.page.total = res.total;
          this.scenesTable.data = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    showAddModal() {
      this.isAddItem = true;
      this.modalTitle = CONSTANTS_CHECK.ADD_CASE;
      delete this.selectCase.scenesId;
      this.selectCase.scenesName = '';
      this.selectCase.extensionTime = 0;
      this.selectCase.shiftLength = 1;
      const arr = [];
      for (var j = 0; j < 4; j++) {
        arr.push(
          {
            shiftName: '',
            shiftStartTime: '',
            shiftEndTime: '',
            shiftSort: (j + 1),
            shiftShowColor: false,
            shifColorArr: [{
              sTime: '',
              eTime: '',
              color: '',
              checkOverTimePush: false
            }]
          }
        );
      }
      this.selectCase.shiftInformationList = arr;

      // this.showScenes.show = true;
      if (this.CHECK_IMG) {
        this.showScenes.show2 = true;
      } else {
        this.showScenes.show = true;
      }
    },
    /**
     * @description: 显示场景模态框
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    showScenesModal() {
      var data = {};

      if (!this.$refs.itemConfig.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.itemConfig.getRadioRecord();
      }
      if (data) {
        this.isAddItem = false;
        const row = JSON.parse(JSON.stringify(data));
        this.oldRow = JSON.parse(JSON.stringify(data));
        row.shiftLength = row.shiftInformationList.length;
        for (var x = 0; x < row.shiftInformationList.length; x++) {
          row.shiftInformationList[x].shiftShowColor = false;
          if (row.shiftInformationList[x].extend01) {
            var colorArr = JSON.parse(row.shiftInformationList[x].extend01);
            row.shiftInformationList[x].shifColorArr = [];
            for (var key in colorArr) {
              var arrTime = key.split('-');
              var overTime = key.substring(12, 13);
              console.log(overTime);
              var overTimePush = false;
              if (Number(overTime) === 1) {
                overTimePush = true;
              }
              row.shiftInformationList[x].shifColorArr.push({
                sTime: arrTime[0],
                eTime: arrTime[1],
                color: colorArr[key],
                checkOverTimePush: overTimePush
              });
            }
          } else {
            row.shiftInformationList[x].shifColorArr = [{
              sTime: '',
              eTime: '',
              color: '',
              checkOverTimePush: false
            }];
          }
        }
        if (row.shiftInformationList.length < 4) {
          var num = 4 - row.shiftInformationList.length;
          for (var i = 0; i < num; i++) {
            row.shiftInformationList.push(
              {
                shiftName: '',
                shiftStartTime: '',
                shiftEndTime: '',
                shiftSort: row.shiftInformationList.length + 1,
                shiftShowColor: false,
                shifColorArr: [{
                  sTime: '',
                  eTime: '',
                  color: '',
                  checkOverTimePush: false
                }]
              }
            );
          }
        }
        setTimeout(() => {
          this.selectCase = row;
        }, 0);
        this.modalTitle = CONSTANTS_CHECK.UPDATE_CASE;
      }
      if (this.CHECK_IMG) {
        this.showScenes.show2 = true;
      } else {
        this.showScenes.show = true;
      }
      // this.showScenes.show = true;
    },
    /**
     * @description: 点检关闭模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clickCreateScenesCancel() {
      this.isAddItem = true;
      // this.selectCase = null;
      // this.showScenes.show = false;
      if (this.CHECK_IMG) {
        this.showScenes.show2 = false;
      } else {
        this.showScenes.show = false;
      }
    },
    /**
     * @description: 新增或修改场景
     * @param data 数据
     * @return: 无
     * @author: ZJM
     *
     * 规则：
     * 1、班次总岂止时间不得超过24小时
     * 2、班次顺序由上至下
     * 3、单班次时间范围不得超过总 24/总班次数  如： 3个班次，则每个班次的时间范围为8个小时
     *
     * 逻辑：
     * 1、将所有时间进行按照班次和上下班时间转成分钟单位并分离成数组，形成一条时间线
     * 2、在分离的遍历中，先进行单班次的时间范围判定，是否超出范围
     *    2.1 班次时间分为上班时间和下班时间，若上班时间比下班时间小，可直接用下班时间减去上班时间
     *    2.2 若上班时间大于下班时间，则证明存在跨天。需要将24-上班时间 将得到值加上下班时间，用这个数据判断是否超出范围
     *    2.3 若上班时间等于下班时间，则默认为跨天，直接判定为超出范围【若仅为一个班次，允许上班时间与下班时间相等，不会判断为超出范围】。
     * 3、由于班次时间是自上而下的，并且用户的选择最一开始的上班时间的范围是从0-24，所以无法具体判断时间重合，用时间轴的追加方式
     * 会将后续的时间拉长，则时间在不存在重合，只需判断整体时间的长度是否超出24小时即可。
     *    3.1 将分离出来的数据进行遍历判断整体时间长度
     *    3.2 每一次遍历，都将当前遍历的数据临存，并进行累加。同时和最开始的时间比较，是否已超出24小时
     *    3.3 遍历中，若当前时间大于前一个时间，则全程时间 += 当前时间 – 上一个时间
     *    3.4 遍历中，若当前时间等于前一个时间，由于在之前的单班次范围进行过过滤，所以 全程时间 += 0
     *    3.5 遍历中，若当前时间小于前一个时间，
     *        则全程时间 += （ 24 * 60 – 前一时间 + 当前时间 ）
     * 4、然后判断全程时间是否超过24小时
     * 注：若只有一个班次，则不存在前后大小的判定之分，一律通过验证。
     */
    addScenes(data) {
      var timeBasic = 24 * 60;
      // 班次时间
      var shiftArr = [];
      // 班次名称
      var shiftNameArr = [];
      // 上下班时间标记
      var shiftTimeArr = [];
      // 同班次
      var sameArr = [];
      // 标记是否错误
      var flagWarn = false;
      // 将所有时间进行按照班次和上下班时间转成分钟单位并分离成数组，形成一条时间线
      for (var item = 0; item < data.shiftLength; item++) {
        // 是否存在班次时间
        if (data.shiftInformationList[item].shiftStartTime) {
          var start = data.shiftInformationList[item].shiftStartTime.split(':')[0] * 60 + (data.shiftInformationList[item].shiftStartTime.split(':')[1] - 0);
          var end = data.shiftInformationList[item].shiftEndTime.split(':')[0] * 60 + (data.shiftInformationList[item].shiftEndTime.split(':')[1] - 0);
          // 在分离的遍历中，先进行单班次的时间范围判定，是否超出范围
          // 班次时间分为上班时间和下班时间，
          // 若上班时间比下班时间小，可直接用下班时间减去上班时间
          if (start < end && (end - start) > (24 / data.shiftLength) * 60) {
            // 若超过班次时间范围
            flagWarn = true;
            showWarningMessage(data.shiftInformationList[item].shiftName + ':' + CONSTANTS_CHECK.ERROR_THREE);
            break;
          } else if (start === end) { // 若上班时间等于下班时间，则默认为阔天，直接判定为超出范围
            if (data.shiftLength !== 1) { // 若只有一个班次，则不会被判断为超出范围
              flagWarn = true;
              showWarningMessage(data.shiftInformationList[item].shiftName + ':' + CONSTANTS_CHECK.ERROR_THREE);
              break;
            }
          } else if (start > end && (timeBasic - start + end) > (24 / data.shiftLength) * 60) { // 若上班时间大于下班时间，则证明存在跨天。需要将24-上班时间 将得到值加上下班时间，用这个数据判断是否超出范围
            flagWarn = true;
            showWarningMessage(data.shiftInformationList[item].shiftName + ':' + CONSTANTS_CHECK.ERROR_THREE);
            break;
          }
          shiftArr.push(start);
          shiftArr.push(end);
          shiftNameArr.push(data.shiftInformationList[item].shiftName);
          shiftNameArr.push(data.shiftInformationList[item].shiftName);
          shiftTimeArr.push(CONSTANTS_CHECK.UP_SHIFT);
          shiftTimeArr.push(CONSTANTS_CHECK.DOWN_NAME);
          sameArr.push(item);
          sameArr.push(item);
        }
      }

      // 如果出现报错，则停止执行
      if (flagWarn) {
        return false;
      }

      const timeData = {
        prevNum: 0, // 上一个时间
        firstNum: 0, // 第一个时间
        allTime: 0 // 全程时间
      };
      // 若只有一个班次，则不存在前后大小的判定之分，一律通过验证。
      if (shiftArr.length > 2) {
        for (var time in shiftArr) {
          // 若上一个时间为0，则赋值
          if (time < 1) {
            timeData.firstNum = shiftArr[time];
            timeData.prevNum = shiftArr[time];
            timeData.allTime = 0;
          } else {
            // 当前时间大于前一个时间
            if (shiftArr[time] > timeData.prevNum) {
              timeData.allTime += (shiftArr[time] - timeData.prevNum);
            } else if (shiftArr[time] === timeData.prevNum) { // 当前时间等于上一个时间
              timeData.allTime += 0;
            } else if (shiftArr[time] < timeData.prevNum) { // 当前时间小于上一个时间
              timeData.allTime += (timeBasic - timeData.prevNum + shiftArr[time]);
            }
            if (timeData.allTime > (24 * 60)) {
              this.shiftWarnMessageTwo(shiftNameArr, shiftTimeArr, time);
              flagWarn = true;
              break;
            }
            timeData.prevNum = shiftArr[time];
          }
        }
      }

      // 如果出现报错，则停止执行
      if (flagWarn) {
        return false;
      }

      var form = {};
      var shiftForm = [];
      form.scenesName = data.scenesName;
      form.scenesRemark = data.scenesRemark;
      form.extensionTime = data.extensionTime;
      // 是否存在场景id
      if (data.scenesId) {
        form.scenesId = data.scenesId;
        // 若选的班次和之前的班次一致
        if (data.shiftLength === this.oldRow.shiftInformationList.length) {
          var arrl = data.shiftInformationList;

          console.log(arrl);
          for (var iy = 0; iy < arrl.length; iy++) {
            var jsonColory = {};
            var colorArry = arrl[iy].shifColorArr;
            for (var c3 = 0; c3 < colorArry.length; c3++) {
              // jsonColory[colorArry[c3].sTime + '-' + colorArry[c3].eTime] = colorArry[c3].color;
              jsonColory[this.funcColorKey(arrl[iy], colorArry[c3])] = this.funcColorValue(colorArry[c3].color);
            }
            data.shiftInformationList[iy].extend01 = JSON.stringify(jsonColory);
          }

          for (var a = 0; a < data.shiftLength; a++) {
            shiftForm.push(data.shiftInformationList[a]);
          }
          // 若选的班次数大于之前的班次数
        } else if (data.shiftLength >= this.oldRow.shiftInformationList.length) {
          // 遍历数据获取需要修改的数据
          for (var b = 0; b < data.shiftLength; b++) {
            const json = {
              shiftName: data.shiftInformationList[b].shiftName,
              shiftStartTime: data.shiftInformationList[b].shiftStartTime,
              shiftEndTime: data.shiftInformationList[b].shiftEndTime,
              shiftSort: data.shiftInformationList[b].shiftSort
            };

            var jsonColor = {};
            var colorArr = data.shiftInformationList[b].shifColorArr;
            for (var c1 = 0; c1 < colorArr.length; c1++) {
              // jsonColor[colorArr[c1].sTime + '-' + colorArr[c1].eTime] = colorArr[c1].color;
              jsonColor[this.funcColorKey(json, colorArr[c1])] = this.funcColorValue(colorArr[c1].color);
            }
            json.extend01 = JSON.stringify(jsonColor);

            if (b <= (this.oldRow.shiftInformationList.length - 1)) {
              json.shiftId = data.shiftInformationList[b].shiftId;
            }
            shiftForm.push(json);
          }
          // 若选择的班次数小于之前的班次数
        } else if (data.shiftLength <= this.oldRow.shiftInformationList.length) {
          var arr = this.oldRow.shiftInformationList;
          for (var i = 0; i < arr.length; i++) {
            const json = {};
            if (i <= (data.shiftLength - 1)) {
              json.shiftId = data.shiftInformationList[i].shiftId;
              json.shiftName = data.shiftInformationList[i].shiftName;
              json.shiftStartTime = data.shiftInformationList[i].shiftStartTime;
              json.shiftEndTime = data.shiftInformationList[i].shiftEndTime;
              json.shiftSort = data.shiftInformationList[i].shiftSort;

              if (data.shiftInformationList[i].shifColorArr) {
                var jsonColorx = {};
                var colorArrx = data.shiftInformationList[i].shifColorArr;
                for (var c2 = 0; c2 < colorArrx.length; c2++) {
                  // jsonColorx[colorArrx[c2].sTime + '-' + colorArrx[c2].eTime] = colorArrx[c2].color;
                  jsonColorx[this.funcColorKey(json, colorArrx[c2])] = this.funcColorValue(colorArrx[c2].color);
                }
                json.extend01 = JSON.stringify(jsonColorx);
              }
            } else {
              json.shiftId = this.oldRow.shiftInformationList[i].shiftId;
              json.state = 0;
            }
            shiftForm.push(json);
          }
        }
      } else { // 若是新增
        var arrlx = data.shiftInformationList;

        console.log(arrlx);

        for (var iyx = 0; iyx < arrlx.length; iyx++) {
          var jsonColoryx = {};
          var colorArryx = arrlx[iyx].shifColorArr;
          for (var c3x = 0; c3x < colorArryx.length; c3x++) {
            // jsonColory[colorArry[c3].sTime + '-' + colorArry[c3].eTime] = colorArry[c3].color;
            jsonColoryx[this.funcColorKey(arrlx[iyx], colorArryx[c3x])] = this.funcColorValue(colorArryx[c3x].color);
          }
          data.shiftInformationList[iyx].extend01 = JSON.stringify(jsonColoryx);
        }

        for (var x = 0; x < data.shiftLength; x++) {
          shiftForm.push(data.shiftInformationList[x]);
        }
      }

      console.log(form, shiftForm);

      scenesInsertOrUpdateScenesAndShiftAction(form, shiftForm).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showScenes.show = false;
          this.getScenesData();
          showSuccessMessage(res.message);
        } else {
          this.showScenes.show = false;
          showWarningMessage(res.message);
        }
      }).catch(err => {
        this.showScenes.show = false;
        showErrorNotify(err);
      });
    },

    funcColorKey(data, json) {
      console.log('lalala', data, json);
      var sTime = '';
      var eTime = '';
      if (json.sTime) {
        sTime = json.sTime;
      } else {
        sTime = data.shiftStartTime;
      }
      if (json.eTime) {
        eTime = json.eTime;
      } else {
        eTime = data.shiftEndTime;
      }
      if (json.checkOverTimePush === true) {
        return sTime + '-' + eTime + '-' + 1;
      } else {
        return sTime + '-' + eTime + '-' + 0;
      }
    },

    funcColorValue(param) {
      if (param) {
        return param;
      } else {
        return '#4caf50';
      }
    },
    /**
     * @description: 提示班次时间冲突
     * @param arr 班次名称
     * @param arrTime 上下班名称
     * @param index 下标
     * @author: ZJM
     */
    shiftWarnMessageOne(arr, arrTime, index) {
      var str = arr[index - 1] + CONSTANTS_CHECK.OF + arrTime[index - 1] + CONSTANTS_CHECK.AND + arr[index] + CONSTANTS_CHECK.OF + arrTime[index] + CONSTANTS_CHECK.ERROR_ONE;
      showWarningMessage(str);
    },
    /**
     * @description: 提示班次时间超过24小时
     * @param arr 班次名称
     * @param arrTime 上下班名称
     * @param index 下标
     * @author: ZJM
     */
    shiftWarnMessageTwo(arr, arrTime, index) {
      var str = arr[0] + CONSTANTS_CHECK.OF + arrTime[0] + CONSTANTS_CHECK.AND + arr[index] + CONSTANTS_CHECK.OF + arrTime[index] + CONSTANTS_CHECK.ERROR_TWO;
      showWarningMessage(str);
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handlescenesTablePageChange(currentPage, pageSize) {
      this.scenesTable.page.currentPage = currentPage;
      this.scenesTable.page.pageSize = pageSize;
      this.getScenesData();
    },
    /**
     * @description: 获取table勾选的数据
     * @param records 勾选数据
     * @return: 无
     * @author: ZJM
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    toDelete() {
      var row = {};

      if (!this.$refs.itemConfig.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.itemConfig.getRadioRecord();
      }
      this.deleteScenes([row.scenesId]);
    },
    /**
     * @description: 删除
     * @param data 选中数据
     * @return: 无
     * @author: ZJM
     */
    deleteScenes(data) {
      console.log(data);
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          scenesDeleteByIdsAction({
            ids: data
          }).then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              this.getScenesData();
              showSuccessMessage(res.message);
            } else {
              showWarningMessage(res.message);
            }
          }).catch(err => {
            showErrorNotify(err);
          });
        })
        .catch(() => { });
    },
    deleteMaintainTime(data) {
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          var deleteItems = [];
          data.forEach(element => {
            deleteItems.push(element.scenesId);
          });
          scenesDeleteByIdsAction({
            ids: deleteItems
          }).then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              this.getScenesData();
              showSuccessMessage(res.message);
            } else {
              showWarningMessage(res.message);
            }
          }).catch(err => {
            showErrorNotify(err);
          });
        })
        .catch(() => { });
    },
    refreshTable() {
      this.getScenesData();
    }
  },
  created() {
    if (SYSTEM_CONFIG[localStorage.getItem('companyId')]) {
      this.CHECK_IMG = SYSTEM_CONFIG[localStorage.getItem('companyId')].CHECK_IMG;
    }
    this.getScenesData();
  }
};
