 <template>
  <el-dialog
    :class="['create-maintaince-option',$store.state.dialogMarginClass]"
    width="80%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
        <vxe-toolbar>
          <template v-slot:buttons>
            <vxe-input
              v-model="filterName"
              type="search"
              placeholder="全表搜索"
              @input="searchAllTable"
            ></vxe-input>
          </template>
        </vxe-toolbar>

      <vxe-table
        border
        show-header-overflow
        show-overflow
        row-key
        height="500px"
        align="center"
        highlight-hover-row
        stripe
        highlight-current-row
        ref="relationTable"
        :data="showList"
        :checkbox-config="{labelField: 'name', checkMethod: checCheckboxkAble}"
        @checkbox-all="selectAllEvent"
        @checkbox-change="selectChangeEvent"
      >
        <vxe-table-column type="checkbox" width="60"></vxe-table-column>
        <!-- <vxe-table-column type="seq" :title="VARIABLE.SEQ" width="60"></vxe-table-column> -->
        <vxe-table-column field="itemId" :title="'ID'" width="80" ></vxe-table-column>
        <vxe-table-column field="itemName" :title="CONSTANTS_CHECK.CHOOSE_CHECK_POINT"></vxe-table-column>
      </vxe-table>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 文字常量
import { VARIABLE, CONSTANTS_CHECK, BTN_TEXT } from '@/utils/constantsFile.js';
import XEUtils from 'xe-utils'; // 注： 在别的组件里调用xe-utils要用this.$utils
export default {
  name: 'CreateMaintainceOptionModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 数据
    itemsList: { type: Array, default: () => [] },
    // 不可用的数据
    disableList: { type: Array, default: () => [] },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      BTN_TEXT: BTN_TEXT,
      filterName: '',
      showList: [],
      tableDatas: []
    };
  },
  created() {
    // 数据写入table
    this.tableDatas = JSON.parse(JSON.stringify(this.itemsList));
    this.showList = JSON.parse(JSON.stringify(this.itemsList));
    // this.searchAllTable();
  },
  methods: {
    searchAllTable() {
      const filterName = XEUtils.toString(this.filterName).trim().toLowerCase();
      if (filterName) {
        // const filterRE = new RegExp(filterName, 'gi');
        const searchProps = ['itemName'];
        const rest = this.tableDatas.filter(item => searchProps.some(key => XEUtils.toString(item[key]).toLowerCase().indexOf(filterName) > -1));
        var arr = rest.map(row => {
          const item = Object.assign({}, row);
          searchProps.forEach(key => {
            // item[key] = XEUtils.toString(item[key]).replace(filterRE, match => `<span class="keyword-lighten">${match}</span>`);
          });
          return item;
        });
        this.showList = arr;
      } else {
        this.showList = JSON.parse(JSON.stringify(this.itemsList));
      }
      for (var i in this.showList) {
        for (var j in this.tableDatas) {
          if (
            this.showList[i].itemId ===
            this.tableDatas[j].itemId && this.tableDatas[j].checked === 1
          ) {
            // this.tableDatas[j].checked = 1;
            this.$refs.relationTable.toggleCheckboxRow(this.showList[i]);
          }
        }
      }
      // return this.tableDatas;
    },
    selectAllEvent({ checked, records }) {
      if (checked) {
        for (var i = 0; i < this.tableDatas.length; i++) {
          this.tableDatas[i].checked = 1;
        }
      } else {
        for (var j = 0; j < this.tableDatas.length; j++) {
          this.tableDatas[j].checked = 0;
        }
      }
    },
    selectChangeEvent({ checked, records, rowIndex, row }) {
      if (checked) {
        for (var i = 0; i < this.tableDatas.length; i++) {
          if (this.tableDatas[i].itemId === row.itemId) {
            this.tableDatas[i].checked = 1;
          }
        }
      } else {
        for (var j = 0; j < this.tableDatas.length; j++) {
          if (this.tableDatas[j].itemId === row.itemId) {
            this.tableDatas[j].checked = 0;
          }
        }
      }
    },
    /**
     * @description: 处理点击取消
     * @return: void
     */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: checkbox是否可选过滤方法
     * @param {Object} row 选中的表行
     * @return: void
     */
    checCheckboxkAble: function({ row }) {
      if (this.disableList.indexOf(row.itemId) > -1) {
        return false;
      } else {
        return true;
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
     */
    confirmAction: function() {
      // console.log(this.tableDatas);
      // console.log(this.showList);
      var selectRecords = this.$refs.relationTable.getCheckboxRecords();
      console.log(selectRecords);
      // var selectRecords2 = this.$refs.relationTable.getTableData();
      // const arr = [];
      // 通过遍历生成新的数据
      // for (var i in this.selectRecords) {
      //   // if (this.selectRecords[i].checked === 1) {
      //   arr.push(this.selectRecords[i]);
      //   // }
      // }
      if (this.handleConfirm) {
        if (selectRecords.length > 0) {
          this.handleConfirm(selectRecords);
        } else {
          // this.handleConfirm(arr);
        }
      }
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
    }
  }
};
</script>
