<template>
  <el-dialog
    :class="['create-maintaince-option',$store.state.dialogMarginClass]"
    width="30%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <el-form  :model="formUpdate" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item :label="CONSTANTS_CHECK.ITEM" prop="itemName" class="content-label">
          <el-input type="textarea" :rows="4" v-model="formUpdate.itemName"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// import func from '../../../vue-temp/vue-editor-bridge';
import { MSG, BTN_TEXT, CONSTANTS_CHECK } from '@/utils/constantsFile';
export default {
  name: 'CreateCheckOptionModal',
  props: {
    // 模态框标题
    title: { type: String, default: '' },
    // 取消按钮
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确定按钮
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 是否显示模态框
    showDialog: { type: Boolean, default: false },
    // 是否新增
    isAdd: { type: Boolean, default: true },
    // 模态框数据
    data: { type: Object, default: () => {} },
    // 取消事件
    handleCancel: Function,
    // 保存事件
    handleConfirm: Function,
    // 关闭事件
    handleClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 点检项名称
      formUpdate: {
        itemName: ''
      },
      itemName: '',
      rules: {
        itemName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' },
          { max: 100, message: '长度不得大于100个字符' }
        ]
      }
    };
  },
  watch: {
    /**
     * @description: 监听点检项
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    data(val, oldVal) {
      if (this.data) {
        this.formUpdate.itemName = this.data.itemName;
        this.itemName = this.data.itemName;
      } else {
        this.formUpdate.itemName = '';
        this.itemName = '';
      }
    }
  },
  methods: {
    /**
     * @description: 点击取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    cancelAction: function() {
      this.$refs.ruleForm.clearValidate();
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 保存
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    confirmAction: function() {
      // if (this.handleConfirm) {
      // }
      this.$refs.ruleForm.validate(valid => {
        // 若验证通过
        if (valid) {
          var data = {
            isAdd: this.isAdd,
            data: this.formUpdate.itemName
          };
          if (!this.isAdd) {
            data.id = this.data.itemId;
          }
          this.handleConfirm(data);
          // this.clearData();
        }
      });
    },
    /**
     * @description: 点击关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    closeAction: function() {
      this.$refs.ruleForm.clearValidate();
      if (this.handleClose) {
        this.handleClose();
      }
    }
  }
};
</script>
