  margin-bottom: 13px;
}

.create-maintaince-option .input-group {
  display: flex;
}

.create-maintaince-option .input-label-1 {
  margin-right: 60px;
  line-height: 30px;
}

.create-maintaince-option .input-label-2 {
  margin-right: 30px;
  line-height: 30px;
}

/* common style may move out */
.create-maintaince-option .el-input__inner {
  height: 30px;
}
.create-maintaince-option .el-dialog__header {
  line-height: initial;
  text-align: initial;
  border-bottom: 1px solid grey;
}
.create-maintaince-option .el-dialog__body {
  line-height: initial;
  text-align: initial;
}
.create-maintaince-option .el-dialog__footer {
  line-height: initial;
  text-align: center;
}

.create-maintaince-option .content {
  margin: 10px;
