// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { SYSTEM_CONFIG } from '@/utils/ajaxConfigure';
// 引入标题元素组件
// import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// import { maintainceOptionService } from '@/services/maintainceOption.service';
// 点检项配置组件
import checkItem from '@/views/main/CheckConfiguration/checkItem.vue';
import checkItemRz from '@/views/main/CheckConfiguration/checkItem_rz.vue';
// 场景组件
import checkScenes from '@/views/main/CheckConfiguration/checkScenes.vue';
// 模板配置组件
import checkTemplate from '@/views/main/CheckConfiguration/checkTemplate.vue';
// 设备类型模板配置
import checkTemplateRelation from '@/views/main/CheckConfiguration/checkTemplateRelation.vue';
import { CONSTANTS_CHECK } from '@/utils/constantsFile';
export default {
  name: 'CheckConfiguration',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    // HeadOperationItem,
    // 保养项配置组件
    checkItem,
    checkItemRz,
    // 保养场景组件
    checkScenes,
    // 保养模板配置组件
    checkTemplate,
    // 设备类型保养配置
    checkTemplateRelation
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      title: CONSTANTS_CHECK.CHECK_MANAGEMENT,
      activeName: 'first',
      activeItem: null,
      table: 1,
      CHECK_IMG: false
    };
  },
  methods: {
    /**
     * @description: 切换tab
     * @param tab tab数据
     * @return:无
     * @author: ZJM
     */
    handleClick(tab, event) {
      if (tab.label === CONSTANTS_CHECK.CHECK_ITEMCONFIG) {
        this.table = 1;
      } else if (tab.label === CONSTANTS_CHECK.CHECK_SCENES) {
        this.table = 2;
      } else if (tab.label === CONSTANTS_CHECK.CHECK_TEMPLATECONFIG) {
        this.table = 3;
      } else if (tab.label === CONSTANTS_CHECK.CHECK_RELATION) {
        this.table = 4;
      }
    },
    initPage() {
      // this.getMaintainOptionData();
    }
  },
  mounted() {
    if (SYSTEM_CONFIG[localStorage.getItem('companyId')]) {
      if (SYSTEM_CONFIG[localStorage.getItem('companyId')].CHECK_IMG) {
        this.CHECK_IMG = SYSTEM_CONFIG[localStorage.getItem('companyId')].CHECK_IMG;
      }
    }

    this.initPage();
  }
};
