<template>
  <el-container>
    <el-header>
      <HeaderTitle class="header-title"></HeaderTitle>
      </el-header>
      <el-main class="view mm">
        <el-row>
          <el-col :span="24">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick" class="mm-tabs">
              <el-tab-pane :label="CONSTANTS_CHECK.CHECK_ITEMCONFIG" name="first">
                <el-row class="tab-details">
                  <checkItem v-if="table==1 && CHECK_IMG==false"></checkItem>
                  <checkItemRz v-if="table==1 && CHECK_IMG==true"></checkItemRz>
                </el-row>
              </el-tab-pane>
              <el-tab-pane :label="CONSTANTS_CHECK.CHECK_SCENES" name="second">
                <el-row class="tab-details">
                  <checkScenes v-if="table==2"></checkScenes>
                </el-row>
              </el-tab-pane>
              <el-tab-pane :label="CONSTANTS_CHECK.CHECK_TEMPLATECONFIG" name="third">
                <el-row class="tab-details">
                    <checkTemplate v-if="table==3"></checkTemplate>
                </el-row>
              </el-tab-pane>
              <el-tab-pane :label="CONSTANTS_CHECK.CHECK_RELATION" name="fourth">
                <el-row class="tab-details">
                  <checkTemplateRelation v-if="table==4"></checkTemplateRelation>
                </el-row>
              </el-tab-pane>
            </el-tabs>
          </el-col>
        </el-row>
      </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/CheckConfiguration/index';
export default js;
</script>

<style lang="less" scoped>
@import url('~@/assets/less/main/CheckConfiguration/index.less');
</style>
