import CreateCheckOptionModal from '@/components/modal/checkOptionModal/createCheckOptionModal';
import { BTN_TEXT, CONSTANTS_CHECK, STATUS_CODE, EXPORT_NAME, MSG, TITLE } from '@/utils/constantsFile';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入类型信息组件
import addItem from '@/views/main/CheckConfiguration/addItem.vue';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { checkItemsGetListAction, checkItemsInsertAction, checkItemsUpdateByIdAction, checkItemsDeleteByIdsAction } from '@/api/checkConfig';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import ImportDialog from '@/components/common/ImportDialog';
import {
  exportExportCheckItemTemplateAction
} from '@/api/export';
import {
  exportImportCheckItemAction
} from '@/api/import';
export default {
  components: {
    CreateCheckOptionModal,
    // 加载table组件
    VXETable,
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    ImportDialog,
    addItem
  },
  props: {
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 模态框标题
      modalTitle: '',
      // table加载动画
      tableLoading: false,
      // 显示模态框
      showCreateCheck: false,
      showPage: 0,
      // 是否新增
      isAddItem: true,
      // 下拉选择
      selectOptions: null,
      // table配置
      optionData: {
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          // { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'itemName',
            title: CONSTANTS_CHECK.ITEM,
            align: 'center'
          }
        ],
        data: []
      },
      selectRecords: [],
      checkOptionDetail: {
        itemId: null,
        itemName: null
      },
      isImportDialogShow: false,
      input_djx: ''
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.showCreateCheckModal(row);
          break;
        case 'delete':
          this.deleteCheckOption([row]);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 显示批量新增
     * @return: 无
     * @author: ZJM
     */
    addItem() {
      this.modalTitle = CONSTANTS_CHECK.ADD_CHECK_POINT;
      this.showPage = 1;
    },
    /**
     * @description: 显示主页
     * @return: 无
     * @author: ZJM
     */
    goBack() {
      this.showPage = 0;
      this.getCheckOptionData();
    },
    /**
     * @description: 展示模态框
     * @param data
     * @return: 无
     * @author: ZJM
     */
    showCreateCheckModal() {
      console.log(777);
      var data = {};

      if (!this.$refs.itemConfig.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.itemConfig.getRadioRecord();
      }
      if (data) {
        this.isAddItem = false;
        this.selectOptions = data;
        this.modalTitle = CONSTANTS_CHECK.UPDATE_CHECK_POINT;
      }
      this.showCreateCheck = true;
    },
    /**
     * @description: 添加或修改点检项
     * @param info 数据
     * @return: 无
     * @author: ZJM
     */
    addOrUpdateCheckOption(info) {
      // 若是新增
      if (info.isAdd) {
        checkItemsInsertAction({
          itemName: info.data,
          extend18: info.extend18
        }).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateCheck = false;
            this.getCheckOptionData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateCheck = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateCheck = false;
          showErrorNotify(err);
        });
      } else { // 若是修改
        checkItemsUpdateByIdAction({
          itemId: info.id,
          itemName: info.data
        }).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateCheck = false;
            this.getCheckOptionData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateCheck = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateCheck = false;
          showErrorNotify(err);
        });
      }
    },
    /**
     * @description: 取消新增
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clickCreateCheckCancel() {
      this.showCreateCheck = false;
      this.isAddItem = true;
      this.selectOptions = null;
    },
    /**
     * @description: 获取table勾选的数据
     * @param records 勾选数据
     * @return: 无
     * @author: ZJM
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    /**
     * @description: 查询点检项列表
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    reset() {
      this.input_djx = '';
      this.getCheckOptionData();
    },
    getCheckOptionData() {
      this.tableLoading = true;
      var params = {};
      if (this.optionData && this.optionData.page) {
        params = {
          current: this.optionData.page.currentPage,
          size: this.optionData.page.pageSize
        };
      }
      var condition = {
        conditionJson1: []
      };

      if (this.input_djx) {
        condition.conditionJson1.push({
          column: 'itemName', // 参数名
          operator: 1, // 并且
          ptype: 'string', // 参数类型
          type: 'like', // 包含
          value: this.input_djx // 参数值
        });
      }
      checkItemsGetListAction(params, condition).then(res => {
        this.tableLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.optionData.data = res.rows;
          this.optionData.page.total = res.total;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        this.tableLoading = false;
        showErrorNotify(err);
      });
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handleOptionTablePageChange(currentPage, pageSize) {
      this.optionData.page.currentPage = currentPage;
      this.optionData.page.pageSize = pageSize;
      this.getCheckOptionData();
    },
    toDelete() {
      var row = {};

      if (!this.$refs.itemConfig.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.itemConfig.getRadioRecord();
      }
      this.deleteCheckOption([row]);
    },
    /**
     * @description: 删除点击项
     * @param data 选中数据
     * @return: 无
     * @author: ZJM
     */
    deleteCheckOption(data) {
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          var deleteItems = [];
          data.forEach(element => {
            deleteItems.push(element.itemId);
          });
          this.setDeleteCheckOption(deleteItems);
        })
        .catch(() => { });
    },
    setDeleteCheckOption(arr) {
      checkItemsDeleteByIdsAction({
        ids: arr
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getCheckOptionData();
          showSuccessMessage(res.message);
        } else {
          this.showCreateCheck = false;
          showWarningMessage(res.message);
        }
      }).catch(err => {
        this.showCreateCheck = false;
        showErrorNotify(err);
      });
    },
    /**
     * @description:显示导入模态框
     * @param {type}
     * @return:
     * @author: ZJM
     */
    importTable() {
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportExportCheckItemTemplateAction();
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.CHECK_TEMPLATE);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:点检项导入提交
     * @param {type}
     * @return:
     * @author: ZJM
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      console.log(fd);
      // 执行接口
      exportImportCheckItemAction({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res);
          } else {
            param.onError(res);
          }
        })
        .catch(err => {
          showWarningMessage(err);
          param.onError(err);
        });
    },
    closeImportDialog() {
      this.getCheckOptionData();
      this.isImportDialogShow = false;
    },
    refreshTable() {
      this.getCheckOptionData();
    }
  },
  created() {
    this.getCheckOptionData();
  }
};
